<template>
    <v-app id="laratime">
        <v-navigation-drawer
            v-model="drawer"
            :clipped="$vuetify.breakpoint.lgAndUp"
            app
        >
            <router-link
                :to="{ name: 'account' }"
                v-slot="{
                    href,
                    route,
                    navigate,
                    isActive,
                    isExactActive
                }"
            >
                <v-list-item>
                    <v-list-item-avatar @click="navigate">
                        <v-img
                            src="https://randomuser.me/api/portraits/men/78.jpg"
                        ></v-img>
                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title @click="navigate">
                            {{ ($store.getters.user || {}).fullname }}
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </router-link>
            <v-divider></v-divider>
            <v-list dense>
                <v-list-item v-for="item in items" :key="item.title" link>
                    <v-list-item-icon>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <v-app-bar :clipped-left="$vuetify.breakpoint.lgAndUp" app>
            <v-app-bar-nav-icon @click="toggleDrawer"></v-app-bar-nav-icon>
            <v-toolbar-title class="headline text-uppercase">
                <span class="font-weight-light">Lara</span>
                <span>Time</span>
            </v-toolbar-title>

            <v-spacer></v-spacer>
            <router-link
                to="/signup"
                v-slot="{ href, route, navigate, isActive, isExactActive }"
            >
                <v-btn
                    @click="navigate"
                    text
                    :class="[
                        isActive && 'router-link-active',
                        isExactActive && 'router-link-exact-active'
                    ]"
                >
                    <span class="mr-2">Create Account</span>
                </v-btn>
            </router-link>
            <v-btn v-if="isLoggedIn" text @click="logout">
                <span class="mr-2">Logout</span>
            </v-btn>
            <router-link
                v-else
                to="/signin"
                v-slot="{ href, route, navigate, isActive, isExactActive }"
            >
                <v-btn text @click="navigate">
                    <span class="mr-2">Login</span>
                </v-btn>
            </router-link>
        </v-app-bar>

        <v-content transition="slide-x-transition">
            <router-view></router-view>
        </v-content>
    </v-app>
</template>

<script>
export default {
    name: 'App',
    computed: {
        isLoggedIn() {
            return this.$store.getters.isLoggedIn;
        }
    },
    created() {
        this.$http.interceptors.response.use(undefined, function(err) {
            return new Promise(function(resolve, reject) {
                console.log(resolve, reject);
                if (
                    err.status === 401 &&
                    err.config &&
                    !err.config.__isRetryRequest
                ) {
                    this.$store.dispatch('logout');
                }
                throw err;
            });
        });
    },
    data: () => ({
        drawer: true,
        items: [{ title: 'My Dashboard', icon: 'mdi-account' }]
    }),
    methods: {
        logout() {
            this.$store.dispatch('logout').then(() => {
                this.$router.push('/signin');
            });
        },
        toggleDrawer() {
            this.drawer = !this.drawer;
        }
    }
};
</script>
<style scoped>
.router-link-active {
    background-color: #efefef;
}
</style>
